<template>
    <div id='Play'>
        <h2>{{p1name}} (สีเหลือง) vs {{p2name}} (สีแดง)</h2>
        <template v-if='state=="p1" || state=="p2"'>
             <h2>สถานะเกม : {{getStatus()}} </h2>
        </template>
        <template v-if='state=="disconnected"'>
            <h2>สถานะเกม : ผู้ต่อสู้ออกจากห้องเสียแล้ว เฮ้อ <a href @click='goToLobby'>กลับไป lobby</a> </h2>
        </template>
        <template v-if='state=="p1win" || state=="p2win"'>
            <h2>สถานะเกม : {{getStatus()}} <a href @click='goToLobby'>กลับไป lobby</a> </h2>
        </template>
        <div style='background-color:#1645dc; width:420px'>
        <template v-for="i in 6">
            <template v-for="j in 7">
                <button :class='getTileClass(i-1,j-1)' @click='insert(j-1)' :value='getBoard(i-1,j-1)' @mouseover='setTopCoinFlag(j-1)' @mouseleave='setTopCoinFlag(-1)'> </button>
            </template>
            <br>
        </template>
        
        </div>
        วิธีเล่น :  <br>
        ผลัดกันหยอดเหรียญคนละตา <br>
        เหรียญใครเรียงติดกัน 4 อันได้ก่อน เป็นผู้ชนะ <br>
        เรียงได้ทั้งแนวนอน แนวตั้ง แนวเฉียง <br>
    </div>
</template>
<script>
export default{
    name:'Play',
    data(){
        return{
            board:null,
            p1name:'',
            p2name:'',
            playerSide:null,
            state:'',
            topCoin:[0,0,0,0,0,0,0],
            topCoinFlag:0
        }
    },
    created(){
        if(this.$playerName==''){
            this.$router.push({name:'login'})
        }
        this.$socket.removeAllListeners()
        this.$socket.on('reportEnemyDisconnected', ()=>{
            if(this.state=='p1' || this.state=='p2')
               this.state = 'disconnected'
        })
        this.$socket.on('setGameData',(data)=>{
            this.board = data.gameData.board
            this.p1name = data.gameData.p1.name
            this.p2name = data.gameData.p2.name
            this.state = data.gameData.state
            this.playerSide = data.playerSide
            this.calTopCoin()
        })
        this.$socket.emit('getGameData')
        setTimeout(()=>{
            if(this.board==null){
                if(this.$playerName=='')
                    this.$router.push({name:'login'})
                else
                    this.$router.push({name:'lobby'})
            }
        }, 1000)
    },
    methods:{
        calTopCoin(){
            for(let j=0;j<7;j++){
                for(let i=5;i>=0;i--){
                    if(this.board[i][j]==0){
                        this.topCoin[j]=i
                        i=-1
                    }else if(i==0){
                        this.topCoin[j]=-1
                    }
                }
            }
        },
        getBoard(i,j){
            if(this.board==null){
                return 0
            }else{
                return this.board[i][j]
            }
        },
        getStatus(){
            if(this.state=='p1'){
                return "ตาของ " + this.p1name + '(สีเหลือง)'
            }else if(this.state=='p2'){
               return "ตาของ " + this.p2name + '(สีแดง)' 
            }else if(this.state=='p1win'){
               return this.p1name + ' เป็นผู้ชนะ!' 
            }else if(this.state=='p2win'){
               return this.p2name + ' เป็นผู้ชนะ!' 
            }
        },
        getTileClass(i,j){
            if(this.board==null){
                return 'tile-empty'
            }
            if(this.topCoin[j]==i && j==this.topCoinFlag && this.isMyTurn()){
                if(this.playerSide==1){
                    return 'tile-yellow'
                }else{
                    return 'tile-red'
                }
            }
            switch (this.board[i][j]){
                case 0: return 'tile-empty'
                case 1: return 'tile-yellow'
                case 2: return 'tile-red'
            }
        },
        goToLobby(e){
            e.preventDefault()
            
            this.$router.nav('lobby')
        },
        insert(i){
            this.$socket.emit('insert',{i})
        },
        isMyTurn(){
            if(this.state=='p1' && this.playerSide==1) return true
            if(this.state=='p2' && this.playerSide==2) return true
            return false
        },
        setTopCoinFlag(v){
            this.topCoinFlag=v
        }
    }

}
</script>
<style>
.tile-empty{
    background-image: url('../assets/tile-empty.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    width:60px;
    height:60px;
    border:none;
    padding:0;
}
.tile-red{
    background-image: url('../assets/tile-red.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    width:60px;
    height:60px;
    border:none;
    padding:0;
}
.tile-yellow{
    background-image: url('../assets/tile-yellow.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    width:60px;
    height:60px;
    border:none;
    padding:0;
}
</style>


