<template>
    <div id='main'>
        
        <textarea v-model='chatbox' rows='20' cols='50'></textarea><br>
        <input v-model='name' type='text' placeholder="type your name!"><br>
        <input v-model='msg' v-on:keydown='sendMsgWithEnter' type='text' placeholder="type some message"> 
        <input v-on:click='sendMsg' type='button' value='send'>

    </div>
</template>
<script>
import util from '@/util'
export default {
    name: 'Main',
    data(){
        return{
            chatbox: '',
            msg: '',
            name: '',
            socket: null
        }
    },
    methods:{
        sendMsg(){
            if(this.name==''){alert('Fill your name bro')}
            else if(this.msg==''){alert('Type some msg bro')}
            else
            this.socket.emit('sendMsg',{data:this.name+':'+this.msg})
        },
        sendMsgWithEnter(e){
            if(e.keyCode==13) this.sendMsg()
        }
    },
    created(){
        util.loadScript('https://cdnjs.cloudflare.com/ajax/libs/socket.io/2.0.4/socket.io.js').
            then(()=>{
                this.socket = io.connect('https://connect4arm.herokuapp.com/')
                //this.socket = io.connect('localhost:3000')
                this.socket.on('broadcast',(data)=>{
                    this.chatbox = ''
                    for(let text of data.chattext){
                        this.chatbox+=text+'\n'
                    }
                })
            })
        
        //this.socket = io.connect()
    }
}
</script>


