<template>
    <div id='Lobby'>
        <h1>หวัดดี {{$playerName}}</h1> <h1>ผู้เล่นออนไลน์ {{nOnline}} คน</h1>
        <chatbox v-bind:texts='chatTexts'></chatbox>
        <input v-model='message' @keydown.enter='submitMessage'>
        <input v-on:click='submitMessage' type='button' value='ส่งข้อความ'>    
        <input v-on:click='hostGame' type='button' value='สร้างเกม'>
    </div>
</template>

<script>
import util from '@/util'
import Chatbox from './dumb/Chatbox'
export default{
    name:'Lobby',
    data(){
        return{
            chatTexts:'',
            message:'',
            nOnline:0
        }
    },
    methods:{
        getSocketId(){
            return this.$socket.id
        },
        hostGame(){
            this.$socket.emit('requestHost')
        },
        submitMessage(){
            if(this.message==''){
                alert('จะไม่พิมพ์สักหน่อยหรอ')
                return
            }
            this.$socket.emit('sendMessage',{message:this.$playerName+' : '+this.message})
            this.message=''
        }
    },
    created(){
        this.$router.nav = name => this.$router.push({name: name})
        if(this.$playerName == ''){
            this.$router.nav('login')
        }
        this.$socket.removeAllListeners()
        this.$socket.emit('setPlayerName', {playerName: this.$playerName})
        this.$socket.emit('requestLeaveGame')
        this.$socket.on('playerJoined', (data)=>{
            this.$router.nav('play')
        })
        this.$socket.on('reportJoin', (data)=>{
            if(data.result==true){
                this.$router.nav('play')
            }
            else{
                alert('too late')
            }
        })
        this.$socket.on('requestName',()=>{
            this.$socket.emit('setPlayerName', {playerName: this.$playerName})
        })  
        this.$socket.on('setnOnline',(data)=>{
            this.nOnline = data.nOnline
        })
        this.$socket.on('setChatTexts',(data)=>{
            this.chatTexts = ""
            for(let text of data.chatTexts) this.chatTexts+=text+'\n'
        })
        
        this.$socket.emit('requestLobby')
    },
    components:{
        'chatbox':Chatbox
    }
}
</script>


