<template>
    <div id='Login'>
        <h1>Connect4</h1>
        <input v-model='name' type='text' placeholder='fill your name' @keydown.enter='submit' autofocus>
        <input v-on:click='submit' type='button' value='GO!!'>
    </div>
</template>

<script>
import Vue from 'vue'
export default{
    name : 'Login',
    data(){
        return{
            name:''
        }
    },
    methods:{
        submit(){
            if(this.name==''){
                alert('please fill the name!')
                return
            }
            Vue.prototype.$playerName = this.name
            this.$router.nav('lobby')
        }
    },
    created(){
        this.$router.nav = name => this.$router.push({name: name})

    }
}
</script>


