import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Login from '@/components/Login'
import Lobby from '@/components/Lobby'
import Play from '@/components/Play'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/lobby',
      name: 'lobby',
      component: Lobby
    },
    {
      path: '/play',
      name: 'play',
      component: Play
    },
    {
      path: '*',
      name: 'login',
      component: Login
    }

  ],
  mode : 'history'
})



