<template>
<div id='Chatbox' ref = 'Chatbox' style="overflow-x:hidden;overflow-y:scroll;border:1px solid grey; width:500px; height:200px;">
    <div v-for="text in chatTexts">
       <div v-if="text.indexOf('Let\'s play!')!=-1">
            <a>{{extractPlayerName(text)}}Let's Play!</a> <input @click='join' type='button' value='join' :name="extractSocketId(text)"><br>
       </div>
       <div v-else>
            <a>{{text}}</a><br>
        </div>
    </div>
</div>
</template>

<script>
export default{
    name:'Chatbox',
    props: ['texts'],
    data(){
        return{
            chatTexts:['init'],
            temp:["dfd","dfdffd","fdfd"]
        }
    },
    methods:{
        extractPlayerName(msg){
            let arr = msg.split("Let\'s play!")        
            return arr[0]
        },
        extractSocketId(msg){           
            let arr = msg.split("Let\'s play!")        
            return arr[1]
        },
        join(e){
            let socketId = e.target.name
            this.$socket.emit("requestJoin",{socketId})
        },
        updateList(v){
            if(v!=null){
                let arr = v.split('\n')
                this.chatTexts=arr
                console.log('updated')
            }            
        }
    },
    watch:{
        texts(newv, oldv){
            this.updateList(newv)   
        }
    },
    created(){
       this.updateList(this.texts)
    },
    updated(){
        this.$refs.Chatbox.scrollTop = this.$refs.Chatbox.scrollHeight
    }
}
</script>



