// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import util from './util'


Vue.config.productionTip = false

util.loadScript('https://cdnjs.cloudflare.com/ajax/libs/socket.io/2.0.4/socket.io.js').then(()=>{
  let socket = io.connect('https://connect4arm.herokuapp.com/')
  //let socket = io.connect('localhost:3000')
  Vue.prototype.$playerName = ''
  Vue.prototype.$socket = socket
  console.log('start new instance' + Math.random())
  new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: { App },
    socket
  })
})






